import React, {useContext} from "react";
import "./MyProjects.scss";
import MyProjectCard from "../../components/myProjectCard/MyProjectCard";
import {myProjects} from "../../portfolio";
import {Fade} from "react-reveal";
import StyleContext from "../../contexts/StyleContext";

export default function MyProjects() {
  const {isDark} = useContext(StyleContext);
  if (myProjects.display) {
    return (
      <div>
        <Fade bottom duration={1000} distance="20px">
          <div className="my-projects-container" id="myprojects-container">
            <div>
              <h1 className="my-projects-heading">Projects</h1>
              <div className="my-projects-cards-div">
                {myProjects.projects.map((card, i) => {
                  return (
                    <MyProjectCard
                      key={i}
                      isDark={isDark}
                      cardInfo={{
                        company: card.company,
                        applogo: card.applogo,
                        role: card.role,
                        date: card.date,
                        desc: card.desc,
                        descBullets: card.descBullets,
                        links: card.links,
                        languages: card.languages,
                      }}
                    />
                  );
                })}
              </div>
            </div>
          </div>
        </Fade>
      </div>
    );
  }
  return null;
}
