import React, { useState, createRef } from "react";
import "./MyProjectCard.scss";
import ColorThief from "colorthief";
import ProjectLanguages from "../../components/projectLanguages/ProjectLanguages";


export default function MyProjectCard({ cardInfo, isDark }) {
  const [colorArrays, setColorArrays] = useState([]);
  const imgRef = createRef();


  function openUrlInNewTab(url) {
    if (!url) {
      return;
    }
    var win = window.open(url, "_blank");
    win.focus();
  }


  function getColorArrays() {
    const colorThief = new ColorThief();
    setColorArrays(colorThief.getColor(imgRef.current));
  }

  function rgb(values) {
    return typeof values === "undefined"
      ? null
      : "rgb(" + values.join(", ") + ")";
  }

  const GetDescBullets = ({ descBullets, isDark }) => {
    return descBullets
      ? descBullets.map((item, i) => (
        <li
          key={i}
          className={isDark ? "subTitle dark-mode-text" : "subTitle"}
        >
          {item}
        </li>
      ))
      : null;
  };

  const GetProjectStoreLinks = ({ links, isDark }) => {
    return links
      ? links.map((link, i) => (
        <li
          key={i}
          className={isDark ? "subTitle dark-mode-text" : "subTitle"}
        >
          {link}
        </li>
      ))
      : null;
  };

  return (
    <div className={isDark ? "my-project-card my-project-card-dark" : "my-project-card my-project-card-light"}>
      <div style={{ background: rgb(colorArrays) }} className="my-project-banner">
        <div className="my-project-blurred_div"></div>

        <div className="my-project-div-company">
          <h5 className="my-project-text-company">{cardInfo.company}</h5>
        </div>

        <img
          crossOrigin={"anonymous"}
          ref={imgRef}
          className="my-project-roundedimg"
          src={cardInfo.applogo}
          alt={cardInfo.company}
          onLoad={() => getColorArrays()}
        />
      </div>



      <div className="my-project-text-details">

        <h5
          className={
            isDark
              ? "my-project-text-role dark-mode-text"
              : "my-project-text-role"
          }
        >
          {cardInfo.role}
          <ProjectLanguages languages={cardInfo.languages} />
        </h5>
        {/* <h5
          className={
            isDark
              ? "my-project-text-date dark-mode-text"
              : "my-project-text-date"
          }
        >
          {cardInfo.date}
        </h5> */}

        <p
          className={
            isDark
              ? "subTitle my-project-text-desc dark-mode-text"
              : "subTitle my-project-text-desc"
          }
        >
          {cardInfo.desc}
        </p>
        <ul>
          <GetDescBullets descBullets={cardInfo.descBullets} isDark={isDark} />
        </ul>

        {cardInfo.links ? (
          <div className="my-project-footer">
            {cardInfo.links.map((link, i) => {
              return (
                <dev className='test' >
                  <dev className='test2'>
                    <span
                      key={i}
                      className={
                        isDark ? "dark-mode my-project-tag" : "my-project-tag"
                      }
                      onClick={() => openUrlInNewTab(link.url)}
                      // style={{ background: rgb(colorArrays),color: rgb(colorArrays.map((c)=> 255 - c)) }}
                      style={{ background: rgb(colorArrays), color: getContrast(rgbToHex(colorArrays[0],colorArrays[1],colorArrays[2],)) }}
                    >
                      {link.name}
                    </span>
                  </dev>
                </dev>
              );
            })}
          </div>
        ) : null}



      </div>
    </div>
  );

  function rgbToHex(r, g, b) {
    return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
  }

  /*!
   * Get the contrasting color for any hex color
   * (c) 2021 Chris Ferdinandi, MIT License, https://gomakethings.com
   * Derived from work by Brian Suda, https://24ways.org/2010/calculating-color-contrast/
   * @param  {String} A hexcolor value
   * @return {String} The contrasting color (black or white)
   */
  function getContrast(hexcolor) {

    // If a leading # is provided, remove it
    if (hexcolor.slice(0, 1) === '#') {
      hexcolor = hexcolor.slice(1);
    }

    // If a three-character hexcode, make six-character
    if (hexcolor.length === 3) {
      hexcolor = hexcolor.split('').map(function (hex) {
        return hex + hex;
      }).join('');
    }

    // Convert to RGB value
    let r = parseInt(hexcolor.substr(0, 2), 16);
    let g = parseInt(hexcolor.substr(2, 2), 16);
    let b = parseInt(hexcolor.substr(4, 2), 16);

    // Get YIQ ratio
    let yiq = ((r * 299) + (g * 587) + (b * 114)) / 1000;

    // Check contrast
    return (yiq >= 128) ? 'black' : 'white';

  }
}
